   /**
    @summary send connection request to photon
    @method RequestConnection
    @param none
    @returns no return
   **/ 
    RequestConnection () {
        if(PhotonRef.state==5 || PhotonRef.isConnectedToMaster()==true || PhotonRef.isInLobby()==true)
            console.log("already connected");
        else
            PhotonRef.start();       
    },

    /**
    @summary Disconnect from photon
    @method DisconnectPhoton
    @param none
    @returns no return
   **/ 
    DisconnectPhoton () {
    if(PhotonRef.isConnectedToMaster()==true || PhotonRef.isInLobby()==true  ||PhotonRef.isJoinedToRoom()==true)
        {
        PhotonRef.disconnect();   
        this.JoinedRoom=false;
        //PhotonRef.leaveRoom();
        this.ResetState();
        }
        else
        {
            console.log("not inside any room or lobby or connected to photon");
        }
    },

    /**
    @summary reseting few values
    @method ResetState
    @param none
    @returns no return
   **/ 
    ResetState()
    {
        this.LeaveRoom=false;    
        this.JoinedRoom=false;
        ShowRoom=false;
        stateText="";
    },

